@import "assets/fonts/style.css";
@import "assets/colors.css";

html, body {
  font-family: 'SF Display', 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
  height: 100vh;
  width: 100%;
}

.navigation-toggle-user, .navigation-menu {
  display: none;
}

.App {
  text-align: center;
}

.clear {
  clear: both;
}

.btn-red {
  color: #cb3636;
  border-color: #cb3636 !important;
}

.error-btn-container {
  height: 127px;
  float: right;
  line-height: 127px;
  padding-right: 23px;
  vertical-align: middle;
}

.wrapper {
  width: 1088px;
  margin: 0 auto;
}

.text-blue {
  color: var(--azul);
}

.btn {
  border-radius: 20px;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
  padding: 8px 15px;
  border: 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
  text-decoration: none;
  background-color: var(--white);
  vertical-align: middle;

  &.blue {
    background-color: #3653cb;
    color: var(--white);
  }

  &.gray {
    background-color: var(--brownish-grey);
    color: var(--white);
  }

  &.bordered {
    border: solid 2px var(--bluey-grey);
  }

  &.icon-button {
    color: var(--bluey-grey);
    padding: 8px 8px;
    line-height: 0;
  }
}

input {
  width: 94%;
  height: 36px;
  border-radius: 5px;
  background-color: #f5f6f7;
  display: block;
  border: 0;
  padding: 0 3%;
}

label {
  display: block;
  font-size: 15px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #2a2f33;
  padding: 20px 0 5px 0;
}

textarea {
  border-radius: 5px;
  background-color: #f5f6f7;
  display: block;
  width: 100%;
  border: 0;
}

.error {
  background: linear-gradient(118deg, #FFFFFF 23%, #F1F4F9 100%);
  overflow: hidden;
  padding: 0;
  margin: 0;
  height: 100vh;
  width: 100%;

  &:before {
    content: ' ';
    position: fixed;
    right: 0;
    top: 0;
    width: 50%;
    height: 100vh;
    background: url("./assets/svg/octagonal-gray.svg") left center no-repeat;
    -webkit-background-size: auto 100%;
    background-size: 205% auto;
  }

  .socials {
    position: fixed;
    right: 10px;
    bottom: 8vh;
    z-index: 1;

    .socialBar {
      border: 1px solid #1d2229;
      width: 0;
      height: 20vh;
      margin: 20px auto;
    }

    .social {
      width: 30px;
      height: 30px;
      display: block;
      color: #1d2229;
      border-radius: 30px;
      border: solid 1px #1d2229;
      text-decoration: none;
      padding: 5px 5px 4px 4px;
      margin: 10px;
      font-size: 18px;
    }
  }

  .contents {
    position: fixed;
    top: 25vh;
    left: 55%;
    z-index: 1;
    text-align: left;
    font-size: 28px;
    right: 10%;

    h1 {
      font-size: 102px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.2;
      letter-spacing: normal;
      text-align: left;
      color: #1d2229;
    }

    h2 {
      font-size: 35px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #1d2229;
    }

    a {
      margin-top: 20px;
      border-radius: 20px;
      box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
      background-color: var(--brownish-grey);
      display: inline-block;
      text-decoration: none;
      padding: 15px 40px;
      color: var(--white);
    }

  }
}

.search {
  width: 100%;
  display: table;
  height: 70px;
  border-radius: 70px;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
  background-color: var(--white);
  z-index: 1;

  i {
    width: 40px;
    height: 40px;
    font-size: 40px;
    padding: 15px;
    cursor: pointer;
    display: table-cell;
  }

  input {
    border: 0;
    font-size: 26px;
    height: 70px;
    background: transparent;
    outline: none;
    padding: 0 10px;
    display: table-cell;
  }

}

.header {
  width: 100%;
  min-height: 333px;
  background-color: var(--pale-grey);
  position: relative;

  .logo {
    height: 110px;
    width: 110px;
    /*margin: -40px 0;*/
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    color: #2a2f33;
    background: url("./assets/logo.svg") left no-repeat no-repeat;
    background-size: 100px 100px;
    display: inline-block;
    float: left;
  }

  .title {
    position: absolute;
    bottom: 55px;

    font-size: 64px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.19;
    letter-spacing: normal;
    text-align: left;
    color: #2a2f33;
  }

  .search {
    position: absolute;
    bottom: -35px;
  }

  .main-menu-bar {
    .menu-bar {
      list-style-type: none;
      margin: 28px 0 0 80px;
      padding: 0;
      overflow: hidden;
      display: inline-block;
      float: left;

      li {
        float: left;

        a {
          display: block;
          padding: 14px 16px;
          text-decoration: none;
          font-size: 19px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.21;
          letter-spacing: normal;
          text-align: center;
          color: #2a2f33;
        }
      }
    }
  }

}

.main-menu-bar {
  .navigation-toggle {
    display: none;
  }
}

.login {
  position: relative;
  height: 100vh;
  min-height: 800px;

  .header {
    background: transparent;
    min-height: 0;

    .logo {
      margin: 0 0 0 139px;
    }
  }

  &:before {
    position: absolute;
    //z-index: -1;
    width: 35%;
    height: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    content: ' ';
    background-image: linear-gradient(149deg, var(--white), var(--pale-grey));
  }

  &:after {
    position: absolute;
    //z-index: -1;
    width: 35%;
    height: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    content: ' ';
    background-image: url('assets/svg/lock.svg');
    background-position: center center;
    background-size: 100% auto;
    background-repeat: no-repeat;
  }

  .content {
    width: 31%;
    float: right;
    margin-top: 50px;
    padding: 0 17%;
  }
}

.menu-bar {
  list-style-type: none;
  margin: 28px 0 0 80px;
  padding: 0;
  overflow: hidden;
  display: inline-block;

  li {
    float: left;

    a {

      display: block;
      padding: 14px 16px;
      text-decoration: none;
      font-size: 19px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.21;
      letter-spacing: normal;
      text-align: center;
      color: #2a2f33;

      &.active {
        text-decoration: underline;
      }
    }
  }
}

.footer {
  .row {
    padding-top: 22px;
    vertical-align: top;
    margin-right: -15px;
    margin-left: -15px;
  }

  .col {
    display: inline-block;
    width: 24%;
    float: left;
    padding-right: 15px;
    padding-left: 15px;
    position: relative;
    box-sizing: border-box;

    &:first-child {
      width: 28%;
    }

    .title {
      font-size: 23px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.22;
      letter-spacing: normal;
      text-align: left;
      color: var(--brown-grey);
      padding: 0 0 10px 0;
    }

    .link {
      font-size: 13px;
      font-weight: 600;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.15;
      letter-spacing: normal;
      text-align: left;
      color: #2a2f33;
      text-decoration: none;
      display: block;
      padding: 5px 0;
    }
  }

  .split {
    clear: both;
    width: 100%;
    margin: 20px auto 0 auto;
    padding-top: 20px;
    height: 0;
    border-bottom: solid 2px var(--bluey-grey);
  }

  .big-text {
    width: 100%;
    font-size: 23px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    text-align: left;
    color: #2a2f33;
  }

  .logo {
    padding: 40px 0 40px 116px;
    font-size: 23px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    text-align: left;
    color: #2a2f33;
    background: url("./assets/logo.svg") left no-repeat no-repeat;
    background-size: 100px 100px;
  }

  .social-links {
    margin-top: 30px;

    .social-link {
      width: 24px;
      height: 24px;
      border: solid 2px #2a2f33;
      line-height: 1.22;
      border-radius: 50px;
      display: inline-block;
      text-decoration: none;
      color: #2a2f33;
      font-size: 21px;
      padding: 10px;
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .copyright {
    margin-top: 50px;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: center;
    color: var(--brown-grey);
  }
}

.accordion {
  width: 100%;
  list-style-type: none;
  box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  padding: 0;
  font-size: 17px;

  li {
    text-align: left;

    .header {
      padding: 10px 40px 10px 80px;
      background-color: var(--white);
      border: solid 1px #e9ecef;
      cursor: pointer;
      min-height: 0;

      &:after {
        content: '\e930';
        margin: 5px -20px 0 0;
        font-family: 'IconsFont' !important;
        color: var(--azul);
        float: right;
      }
    }

    .content {
      overflow: hidden;
      font-size: 16px;
      transition: max-height 0.2s;
      background-color: #edf3fe;
      max-height: 0;
      line-height: 24px;
    }

    &:not(first-child) .header {
      border-top: 0;
    }

    .content .wrapper {
      padding: 10px 10px 10px 80px;
    }

    &.active {
      .header:after {
        content: '\e934';
      }

      .content {
        max-height: 600px;
      }
    }

    &:first-child .header {
      border-radius: 10px 10px 0 0;
    }

    &:last-child .header {
      border-radius: 0 0 10px 10px;
    }

    &.active:last-child {
      .header {
        border-radius: 0;
      }

      .content {
        border-radius: 0 0 10px 10px;
      }
    }
  }
}

.faq {
  margin: 0;
  padding: 0;

  .support {
    font-size: 13px;
    color: #2a2f33;
    font-weight: 600;
    text-align: left;
    font-family: SFProDisplay;
  }
}

.main {
  min-height: 100vh;
  margin: auto;

  &.index {
    .header {
      background-image: linear-gradient(118deg, var(--white) 23%, var(--pale-grey) 100%);

      .title {
        position: relative;
      }

      .slider {
        min-height: 470px;

        .left {
          margin-top: 210px;
          width: 60%;
        }

        .image {
          position: absolute;
          right: 0;
          top: -30px;
          bottom: 0;
          width: 60%;
          background: url("./assets/svg/errors/error_comming.svg") center center no-repeat;
        }

        h1 {
          font-size: 54px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.19;
          letter-spacing: normal;
          text-align: left;
          color: #2a2f33;
        }

        h2 {
          font-size: 26px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.19;
          letter-spacing: normal;
          text-align: left;
          color: var(--bluey-grey);
          width: 60%;
        }
      }
    }
  }

  .footer {
    background: var(--pale-grey);
  }

  .style {
    position: relative;
    min-height: 275px;
    padding: 140px 0;

    &:before {
      content: ' ';
      width: 322px;
      height: 244px;
      background: url("./assets/svg/fire.svg");
      -webkit-background-size: 322px 244px;
      background-size: 322px 244px;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: -1;
    }

    &:after {
      content: ' ';
      width: 566px;
      height: 435px;
      background: url("./assets/svg/octagonal-gray.svg");
      -webkit-background-size: 1140px 1140px;
      background-size: 1140px 1140px;
      background-position-x: 0;
      background-position-y: 435px;
      position: absolute;
      right: 0;
      top: 0;
      z-index: -1;
    }

  }
}

.main-content {
  margin-top: -69px;

  h1 {
    font-size: 40px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: #2a2f33;
  }

  h3 {
    font-size: 20px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: var(--bluey-grey);
  }

  .cols {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 20px;

    .col {
      flex: 0 0 20%;

      h1 {
        font-size: 30px;
      }
    }
  }

  .circle {
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.16);
    background-color: #f9fbfd;
