}

.panel {
  margin-top: 26px;
  width: 100%;
  border-radius: 10px;
  box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
  background-color: var(--white);

  .panel-content {
    padding: 46px;
  }
}

.user {
  position: relative;
  min-height: 520px;
  padding: 30px 0 30px 78px;
  transition: padding 0.5s;
  background-color: var(--pale-grey);

  .panel-header {
    position: absolute;
    top: 0;
    z-index: 2;
    left: 0;
    right: 0;
    height: 82px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);
  }

  .navigation {
    position: absolute;
    list-style-type: none;
    top: 0;
    bottom: 0;
    left: 0;
    width: 78px;
    box-shadow: 0 -15px 15px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);
    padding: 25px 0;
    margin: 0;
    z-index: 1;
    transition: width 0.5s;
    overflow: hidden;

    .active-show {
      display: none;
    }

    span {
      display: inline-block;
    }

    li {
      margin-top: 53px;
      white-space: nowrap;
      text-align: left;

      &:first-child {
        margin-top: 0;
      }

      button {
        border: 0;
        background: transparent;
        padding: 0;
        margin: 0;
        outline: none;
      }

      button, a {

        color: var(--brownish-grey);
        text-decoration: none;
        text-align: left;

        i {
          width: 78px;
          display: inline-block;
          text-align: center;
          font-size: 25px;
        }

        &.active {
          color: var(--azul);
        }
      }
    }
  }

  &.active .navigation {
    width: 208px;
  }

  &.active {
    padding-left: 208px;

    .active-show {
      display: block;
    }

    .active-hide {
      display: none;
    }
  }

  .content {
    padding: 0 35px;
    margin: auto;
    text-align: left;

    label {
      width: 50%;
      padding: 20px 2% 5px 2%;
      display: inline-block;
    }
  }

  .address-container {
    display: flex;
    height: 70px;
    margin-top: 40px;

    .admin-actions {
      float: right;
      width: auto;
      height: 70px;

      a, button {
        width: 38px;
        height: 70px;
        line-height: 70px;
        padding: 0 16px;
        color: black;
        font-size: 38px;
        text-decoration: none;
        text-underline: none;

        &.active {
          color: var(--azul);
        }
      }
    }

    .address {
      text-align: center;
      font-size: 26px;
      letter-spacing: 8.58px;
      line-height: 70px;
      width: 100%;
      height: 70px;
      border-radius: 35px;
      box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
      background-color: var(--white);
      padding: 0 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .card-row {
    margin: 0 -15px;

    .card-col {
      float: left;
      width: 20%;
      margin: 30px 0 0 0;

      &.custom {
        width: 40%;

        .card-holder {
          -webkit-transition: all 200ms ease-in;
          -moz-transition: all 200ms ease-in;
          transition: all 200ms ease-in;
          -webkit-transform: scale(1);
          -ms-transform: scale(1);
          -moz-transform: scale(1);
          height: 115px;
          margin-top: 20px;

          h1 {
            margin: 0 0 0 30px;
            height: 45px;
          }

          .row-content {
            height: 70px;
            border-radius: 35px;
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
            background-color: var(--white);
            line-height: 70px;

            span {
              //word-break:break-all;
              display: block;
              padding: 0 15px;
              text-align: center;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }

          &:first-child {
            margin-top: 0;
          }

          &:hover {
            z-index: 2;
            //-webkit-transform: scale(1.1);
            //-ms-transform: scale(1.1);
            //-moz-transform: scale(1.1);
            //transform: scale(1.1);
          }
        }
      }

      .card {
        margin: 0 15px;
        height: 250px;
        box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
        background-color: var(--white);
        -webkit-transition: all 200ms ease-in;
        -moz-transition: all 200ms ease-in;
        transition: all 200ms ease-in;
        text-align: center;
        -webkit-transform: scale(1);
        -ms-transform: scale(1);
        -moz-transform: scale(1);
        transform: scale(1);
        border-radius: 10px;

        .card-wrapper {
          padding: 10px 15px;

          .card-title {
            font-size: 18px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.17;
            color: #2a2f33;
            margin-top: 10px;
          }

          .card-content {
            margin-top: 10px;
            font-size: 25px;

            &.sublime {
              font-size: 18px;
            }

            .timer-hour, .timer-minute, .timer-second {
              display: inline-block;
              font-size: 22px;
              font-weight: 500;
              font-stretch: normal;
              font-style: normal;
              line-height: 1.2;
              letter-spacing: normal;
              text-align: center;
              color: #2a2f33;
            }

            .timer-hour {
              font-size: 40px;
            }
          }

          .card-title2 {
            font-size: 20px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.2;
            letter-spacing: normal;
            color: #2a2f33;
            margin-top: 25px;
          }

          .card-content i {
            font-size: 13px;
            margin-left: 10px;
          }

          .card-filter {
            margin-top: 54px;
            font-size: 11px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.18;
            letter-spacing: normal;
            color: #2a2f33;
          }
        }

        &:hover {
          z-index: 2;
          -webkit-transform: scale(1.1);
          -ms-transform: scale(1.1);
          -moz-transform: scale(1.1);
          transform: scale(1.1);
        }

        &.card-half {
          height: 115px;
          margin-top: 20px;

          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
  }

  .split-panel {
    .split-panel-form {
      float: left;
      width: 30%;
      padding: 0 5% 0 15%;
      border-right: solid 2px var(--bluey-grey);

      label {
        margin-bottom: 10px;
        width: 100%;
      }

      input {
        padding: 0 10px;
      }

      .btn {
        display: inline-block;
        padding: 8px 40px;
      }
    }

    .split-panel-text {
      float: right;
      width: 45%;
      font-size: 16px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.19;
      letter-spacing: normal;
      text-align: left;
      color: #2a2f33;
    }
  }

  .chartFilter {
    float: right;
    margin-top: 30px;
  }

  .footer {
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);
  }
}

.chart-filter {
  float: right;
  margin-top: 30px;
  position: relative;
  //display: inline-block;
  color: black;
  padding: 15px;
