      float: right;
      background-size: 64px 64px;
      margin-top: 9px;
      background-repeat: no-repeat;
      background-position: center center;
      position: relative;

      i {
        width: 22px;
        height: 22px;
        box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.16);
        background-color: var(--white);
        border: solid 1px #e1e1e1;
        border-radius: 12px;
        position: absolute;
        line-height: 22px;
        right: 0;
        bottom: 0;
      }

      .pr-info {
        position: absolute;
        width: 200px;
        padding: 11px;
        display: none;
        top: 74px;
        right: -10px;
        opacity: 0.97;
        box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
        border: solid 1px #e1e1e1;
        background-color: var(--white);
        border-radius: 5px;

        &:before {
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 10px solid #e1e1e1;
          content: ' ';
          position: absolute;
          top: -10px;
          right: 10px;
        }

        &:after {
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 10px solid var(--white);
          content: ' ';
          position: absolute;
          top: -9px;
          right: 10px;
          z-index: 10;
        }
      }

      &:hover .pr-info {
        display: block;
      }
